import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import * as yup from "yup";
import { DotWave } from '@uiball/loaders'
import { RealTypo } from "./components/RealTypo"
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';
import Grid from "@material-ui/core/Grid";
import Stack from '@mui/material/Stack';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';

const schema = yup.object().shape({
  textRec: yup
    .string()
    .test('len', 'נא לא לכתוב מעל 400 תווים', val => val.length <= 400),
  ptorRec: yup
    .string(),
  rav: yup
    .string(),
  hoger: yup
    .string(),

});


export const Recommend = () => {
  const [value, setValue] = useState(new Date());
  const [isLoading, setLoading] = useState(false);
  const { setValues, data } = useData();
  const history = useNavigate();
  const { register, handleSubmit, errors } = useForm({
    defaultValues: {
      textRec: data.textRec,
      ptorRec: data.ptorRec,
      rav: data.rav,
      hoger: data.hoger,
      isWaiting: data.isWaiting
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });
  const [ptorRec, setptorRec] = useState(data.ptorRec);
  const handleChangeptorRec = (event) => {
    setptorRec(event.target.value);
    console.log(ptorRec);
  };
  const [rav, setrav] = useState(data.rav);
  const handleChangerav = (event) => {
    setrav(event.target.value);
  };
  const [hoger, sethoger] = useState(data.hoger);
  const handleChangehoger = (event) => {
    sethoger(event.target.value);
  };
  console.log(data);
  const onSubmit = async (data) => {
    console.log(data);
    // data["isWaiting"] = "false"
    setValues(data);
    console.log(data);
    history("/MPSignature");
  };


  return (
    <MainContainer >
      <Stack
        spacing={2}
      >
        <RealTypo component="h3" variant="h5">הזן את המספר האישי של החייל שברצונך לאשר</RealTypo>
      </Stack>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Stack
          spacing={2}
        >
          <Input2
            multiline
            rows={4}
            placeholder="כתוב כאן את המלצתך"
            dir="rtl"
            ref={register}
            id="textRec"
            type="string"
            label="התייחסותך"
            name="textRec"
            error={!!errors.textRec}
            helperText={errors?.textRec?.message}
          />

          <Grid item>
            <FormLabel>האם אתה ממליץ על הפטור?</FormLabel>
            <RadioGroup
              name="ptorRec"
            >
              <FormControlLabel value="true" control={
                <Radio
                  name="ptorRec"
                  inputRef={register}
                  value='true'
                  checked={ptorRec === 'true'}
                  onChange={handleChangeptorRec}
                />} label="ממליץ" />
              <FormControlLabel value="false" control={
                <Radio
                  name="ptorRec"
                  inputRef={register}
                  value="false"
                  checked={ptorRec === "false"}
                  onChange={handleChangeptorRec}
                />} label="לא ממליץ" />
            </RadioGroup>
          </Grid>

          <Grid item>
            <FormLabel >האם לחייל יש זקן בתעודת החוגר?</FormLabel>
            <RadioGroup
              name="hoger"

            >
              <FormControlLabel value="true" control={
                <Radio
                  name="hoger"
                  inputRef={register}
                  value='true'
                  checked={hoger === 'true'}
                  onChange={handleChangehoger}
                />} label="כן" />
              <FormControlLabel value="false" control={
                <Radio
                  name="hoger"
                  inputRef={register}
                  value="false"
                  checked={hoger === "false"}
                  onChange={handleChangehoger}
                />} label="לא" />
            </RadioGroup>
          </Grid>

          <Grid item>
            <FormLabel>האם אתה ממליץ לרב היחידתי לקיים שיחה עם החייל?</FormLabel>
            <RadioGroup
              name="rav"

            >
              <FormControlLabel value="true" control={
                <Radio
                  name="rav"
                  inputRef={register}
                  value='true'
                  checked={rav === 'true'}
                  onChange={handleChangerav}
                />} label="ממליץ" />
              <FormControlLabel value="false" control={
                <Radio
                  name="rav"
                  inputRef={register}
                  value="false"
                  checked={rav === "false"}
                  onChange={handleChangerav}
                />} label="לא ממליץ" />
            </RadioGroup>
          </Grid>
          <Grid item container justify="center" xs={8}>
                  <LocalizationProvider dateAdapter={AdapterDateFns}>

                    <DatePicker
                      dir="rtl"
                      label="תאריך התייחסות"
                      id="date"
                      inputFormat="dd/MM/yyyy"
                      value={value}
                      inputRef={register}
                      onChange={(newValue) => {
                        setValue(newValue);
                      }}
                      renderInput={(params) => <Input2 name="mpDate"{...params} />}
                    />
                  </LocalizationProvider>
                </Grid>

          <PrimaryButton>
            {isLoading && (
              <DotWave
                size={40}
                speed={2.5}
                color="gray"
              />
            )}
            {!isLoading && <span>כניסה</span>}
          </PrimaryButton>
        </Stack>
      </Form>
    </MainContainer>
  );
};
