import React, { useRef, useState } from "react";
import { DotWave } from '@uiball/loaders';
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useData } from "./DataContext";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import SignaturePad from 'react-signature-pad-wrapper'
import { Box } from "@mui/system";
import { Grid } from "@mui/material";
import { Delete } from "@material-ui/icons";
import { SecondryButton } from "./components/SecondryButton";
import Swal from "sweetalert2";
import auth from "./components/routing/auth";
import {RealTypo} from "./components/RealTypo"


export const MPSignature = () => {
  

  const [isLoading, setLoading] = useState(false);
  const {data} = useData();
  const history = useNavigate();
  
  let sigData = '';
  const { handleSubmit } = useForm({
    defaultValues: {
      mpSig: data.mpSig,
    },
    mode: "onChange",
  });
  let sigPad = useRef({});
  console.log(data);

  // const onSubmit = (data) => {
  //   data["imageData"] = data.sigData
  //   console.log(data);
  //   setValues(data);
  //   console.log(data);
  //   history("/step3");
  //   setValues(data);
  // };
  
  
  function clearSig() {
    sigPad.current.clear();
  }
  function registerSig() {
    sigData = sigPad.current.toDataURL();
    if (!sigPad.current.isEmpty()) {
      data["mpSig"] = sigData
      console.log(sigData)
      console.log(data)
      history("/result");
    }
    else {
      setLoading(false);
      auth.logout(() => { history("/MPSignature"); });
      Swal.fire({
        direction:'rtl',
        icon: "error",
        title: "חובה לחתום על המסמך!",
        confirmButtonText: "אוקיי"
      });
    }
  }
  
  


  return (
    
    <MainContainer>
  
      <RealTypo component="h2" variant="h5">
        חתום כאן
      </RealTypo>
      <Form onSubmit={handleSubmit(registerSig)}>
       <Box border={1}>
            <SignaturePad
            callResizeHandler
            options={{ minWidth: 1, maxWidth: 4,throttle:0, penColour: '#2c3e50'}}
            ref={sigPad}
            />
            </Box>
        
        <Grid container direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={2}>
          <Grid justifyContent="center"
            alignItems="center" item xs={4} sm={4} md={4}>
              <PrimaryButton disabled={isLoading}>
          {isLoading && (
            <DotWave
              size={40}
              speed={2.5}
              color="gray"
            />
          )}
                {!isLoading && <span>המשך</span>}</PrimaryButton>
          </Grid>
          <Grid justifyContent="center"
            alignItems="center" item xs={4} sm={4} md={4}>
              
                <SecondryButton
                startIcon={<Delete />}
                dir='ltr'
              onClick={clearSig}
              >
                נקה
            </SecondryButton>
          </Grid>
        </Grid>
        </Form>
    </MainContainer>
  );
};
