import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import * as yup from "yup";
import auth from "./components/routing/auth";
import { collection, where, getDocs, query } from "firebase/firestore";
import { db } from "./base.js"
import Swal from "sweetalert2";
import { DotWave } from '@uiball/loaders'
import {RealTypo} from "./components/RealTypo"
export const hasEmailString = (data2) => {
  {
    var objIndex = Object.values(data2);
    var keyIndex = Object.keys(data2).indexOf('hasEmail');
    let dupValue = objIndex[keyIndex];
    console.log(dupValue);
    if (dupValue === 'true')
      data2["hasEmail"] = true;
    if (dupValue === 'false')
      data2["hasEmail"] = false;
  }
  {
     objIndex = Object.values(data2);
   keyIndex = Object.keys(data2).indexOf('isDati');
    let dupValue = objIndex[keyIndex];
    console.log(dupValue);
    if (dupValue === 'true')
      data2["isDati"] = true;
    if (dupValue === 'false')
      data2["isDati"] = false;
  }
  return data2;
}


const schema = yup.object().shape({
  personalID: yup
    .number()
    .typeError('המספר האישי חייב להיות מספר')
    .test('len', 'המספר האישי חייב להכיל 7 ספרות', val => val.toString().length === 7),
});

export const Login = () => {
  const [isLoading, setLoading] = useState(false);
  const { setValues, data, overRideValues } = useData();
  const history = useNavigate();
  console.log("data is now="+data)
  const { register, handleSubmit, errors } = useForm({
    defaultValues: {
      personalID: 9999999
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });

  const collectionRef = collection(db, "Members")
  const acollectionRef = collection(db, "Admins")
   
  let newData = async (data2) => {
    let info = Object.values(data2).toString();
    let q = query(collectionRef, where("personalID", "==", info));
    let querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc1) => {
      data2 = doc1.data();
    });

    await hasEmailString(data2);

    return data2;
  };


  let adminCheck = async (data2) => {
    let info = Object.values(data2).toString();
    let a = query(acollectionRef, where("mpID", "==", info));
    let squerySnapshot = await getDocs(a);
    squerySnapshot.forEach((doc1) => {
      data2 = doc1.data();
    });
    console.log(data2)
    return data2;
  };

  const onSubmit = async (data) => {
    setLoading(true);
    let data2 = await newData(data);
    let data3 = await adminCheck(data);

    if (data !== data2) {
      setLoading(false);
      auth.logout(() => { history("./"); });
      Swal.fire({
        icon: "error",
        title: "החייל כבר מילא בקשה בעבר!",
        text: "במקרה ומדובר בשגיאה, אנא תקן זאת בחלון המפקדים.",
        confirmButtonText: "אוקיי"
      });
    }
    else {
      if (data !== data3) {
        setLoading(false);
        auth.logout(() => { history("./"); });
        Swal.fire({
          icon: "success",
          title: data3["mpFirstName"] + " " + data3["mpLastName"] + ", ברוך הבא!",
          text: "לחץ אוקיי כדי לעבור לחלון לאישור חיילים",
          confirmButtonText: "אוקיי"
        });
        auth.login(() => { history("./PickAsolider"); })
        setValues(data3);   
      }
      else {
        auth.login(() => { history("./step1"); })
      }
      setValues(data2);   
    };
  };


  return (
    <MainContainer {...overRideValues(data)} >
      <RealTypo component="h2" variant="h5">הזדהות</RealTypo>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Input2
          dir="rtl"
          ref={register}
          id="personalID"
          type="number"
          label="מספר אישי"
          name="personalID"
          error={!!errors.personalID}
          helperText={errors?.personalID?.message}
        />

        <PrimaryButton  disabled={isLoading}>
          {isLoading && (
            <DotWave
              size={40}
              speed={2.5}
              color="gray"
            />
          )}
          {!isLoading && <span>כניסה</span>}
        </PrimaryButton >
      </Form>
    </MainContainer>
  );
};
