import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import * as yup from "yup";
import { collection, where, getDocs, query } from "firebase/firestore";
import { db } from "./base.js"
import Swal from "sweetalert2";
import { DotWave } from '@uiball/loaders'
import {RealTypo} from "./components/RealTypo"

const schema = yup.object().shape({
  personalID: yup
    .number()
    .typeError('המספר האישי חייב להיות מספר')
    .test('len', 'המספר האישי חייב להכיל 7 ספרות', val => val.toString().length === 7),
});
export const isWaitingString = (data2) => {
    console.log(data2);
    let dataValue = data2;
    var objIndex = Object.values(dataValue);
    var keyIndex = Object.keys(dataValue).indexOf('isWaiting');
    let dupValue = objIndex[keyIndex];
    console.log(dupValue);
  if (dupValue === 'true') {
    console.log(data2)
    return data2
  }
    if (dupValue === 'false')
      return false
    if (dupValue === undefined)
      return false
}
export const PickAsolider = () => {
  const [isLoading, setLoading] = useState(false);
  const { setValues, data } = useData();
  const history = useNavigate();
  const { register, handleSubmit, errors } = useForm({
    defaultValues: {
      personalID: 8593827
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });

  const collectionRef = collection(db, "Members")
  
  let newData = async (data2) => {
    let info = Object.values(data2).toString();
    let q = query(collectionRef, where("personalID", "==", info));
    let querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc1) => {
      data2 = doc1.data();
    });
    let result = isWaitingString(data2);
    console.log(result)
    return result;
  };

  const onSubmit = async (data) => {
    setLoading(true);
    
    let data2 = await newData(data)
    console.log(data2)
    if (data2 === false) {
      setLoading(false);
      Swal.fire({
        icon: "error",
        title: "לא קיים חייל עם מ.א. זה שממתין לאישור",
        text: "יכול להיות שהמספר הוזן בצורה שגויה. אנא נסה שנית.",
        confirmButtonText: "אוקיי"
      });
      history("/PickAsolider")
    }
    else {
    console.log(data2);
    setValues(data2);
    history("/Recommend");
    }
    
  };


  return (
    <MainContainer >
      <RealTypo component="h3" variant="h5">הזן את המספר האישי של החייל שברצונך לאשר</RealTypo>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Input2
          dir="rtl"
          ref={register}
          id="personalID"
          type="number"
          label="מספר אישי"
          name="personalID"
          error={!!errors.personalID}
          helperText={errors?.personalID?.message}
        />

        <PrimaryButton>
          {isLoading && (
            <DotWave
              size={40}
              speed={2.5}
              color="gray"
            />
          )}
          {!isLoading && <span>כניסה</span>}
        </PrimaryButton>
      </Form>
    </MainContainer>
  );
};
